.TodoSearch{
    background: #25273c;
    border-radius: 2px;
    border: 2px solid #202329;
    height: 64px;
    width:  100%;
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: white;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder{
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}
