.TodoItem{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-top: 1px solid gray; /* Borde superior gris */
    border-bottom: 1px solid gray; /* Borde inferior gris */
}

.TodoItem-p{
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete{
    text-decoration: line-through;
}

